
import React from 'react';
import Link from 'next/link';
import Image from 'next/image';

export default function SolutionsPage() {
    return (
        <main className="bg-white">
            <section className="relative bg-brand-dark py-20 lg:py-32 overflow-hidden text-white">
                <div className="absolute inset-0 bg-[url('/images/logo_origin.webp')] bg-no-repeat bg-right-top opacity-5 pointer-events-none mix-blend-overlay w-full h-full transform scale-150 translate-x-1/4 -translate-y-1/4"></div>
                <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 relative z-10 text-center">
                    <span className="text-brand-gold font-bold uppercase tracking-widest text-sm mb-4 block">Overview</span>
                    <h1 className="text-4xl md:text-5xl lg:text-6xl font-serif font-bold mb-6 text-white text-shadow-lg">Our Solutions</h1>
                    <p className="text-xl text-slate-300 max-w-3xl mx-auto font-light leading-relaxed">
                        Comprehensive strategies designed to optimize corporate wealth and tax efficiency.
                    </p>
                </div>
            </section>

            <section className="py-20 lg:py-24">
                <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
                    <div className="grid grid-cols-1 md:grid-cols-3 gap-10">
                        {/* Corporate Par */}
                        <div className="group bg-white rounded-xl shadow-lg border border-slate-100 overflow-hidden hover:shadow-2xl transition duration-500 hover:-translate-y-2 flex flex-col h-full">
                            <div className="h-56 relative overflow-hidden">
                                <Image
                                    src="/images/corporate-par-insurance.png"
                                    alt="Corporate Par"
                                    fill
                                    className="object-cover transition duration-700 group-hover:scale-110"
                                />
                                <div className="absolute inset-0 bg-brand-blue/20 group-hover:bg-transparent transition duration-500"></div>
                            </div>
                            <div className="p-8 flex-1 flex flex-col">
                                <h3 className="text-2xl font-serif font-bold text-brand-blue mb-4">Corporate Par</h3>
                                <p className="text-slate-600 mb-6 flex-grow">
                                    A stable, tax-efficient asset for Canadian business owners. Combines insurance protection with steady, tax-advantaged asset growth inside the corporation.
                                </p>
                                <Link href="/corporate-par" className="mt-auto inline-block text-center w-full py-3 border border-brand-blue text-brand-blue font-bold rounded hover:bg-brand-blue hover:text-white transition">
                                    Learn More
                                </Link>
                            </div>
                        </div>

                        {/* CDA */}
                        <div className="group bg-white rounded-xl shadow-lg border border-slate-100 overflow-hidden hover:shadow-2xl transition duration-500 hover:-translate-y-2 flex flex-col h-full">
                            <div className="h-56 relative overflow-hidden">
                                <Image
                                    src="/images/cda-planning.png"
                                    alt="CDA Planning"
                                    fill
                                    className="object-cover transition duration-700 group-hover:scale-110"
                                />
                                <div className="absolute inset-0 bg-brand-gold/20 group-hover:bg-transparent transition duration-500"></div>
                            </div>
                            <div className="p-8 flex-1 flex flex-col">
                                <h3 className="text-2xl font-serif font-bold text-brand-blue mb-4">CDA Planning</h3>
                                <p className="text-slate-600 mb-6 flex-grow">
                                    Tax-free corporate wealth extraction. Capital Dividend Account (CDA) allows business owners to extract corporate wealth completely tax-free.
                                </p>
                                <Link href="/cda" className="mt-auto inline-block text-center w-full py-3 border border-brand-blue text-brand-blue font-bold rounded hover:bg-brand-blue hover:text-white transition">
                                    Learn More
                                </Link>
                            </div>
                        </div>

                        {/* IFA */}
                        <div className="group bg-white rounded-xl shadow-lg border border-slate-100 overflow-hidden hover:shadow-2xl transition duration-500 hover:-translate-y-2 flex flex-col h-full">
                            <div className="h-56 relative overflow-hidden">
                                <Image
                                    src="/images/ifa-strategy.png"
                                    alt="IFA Strategy"
                                    fill
                                    className="object-cover transition duration-700 group-hover:scale-110"
                                />
                                <div className="absolute inset-0 bg-brand-blue/20 group-hover:bg-transparent transition duration-500"></div>
                            </div>
                            <div className="p-8 flex-1 flex flex-col">
                                <h3 className="text-2xl font-serif font-bold text-brand-blue mb-4">IFA Strategy</h3>
                                <p className="text-slate-600 mb-6 flex-grow">
                                    Immediate Financing Arrangement. A liquidity strategy designed for those who value both cash flow and the compounding advantages of permanent insurance.
                                </p>
                                <Link href="/ifa" className="mt-auto inline-block text-center w-full py-3 border border-brand-blue text-brand-blue font-bold rounded hover:bg-brand-blue hover:text-white transition">
                                    Learn More
                                </Link>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </main>
    );
}
